html,
body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

body {
  color: white;
  background-color: #6fa067;
  font-family: Roboto,sans-serif;
}

.wrapper {
  min-height: 100%;
  min-width: 100%;
}

#chart {
  position: absolute;
}

#square {
  overflow: hidden;
  width: 95vw;
  height: 95vw;
  transition: .1s;
  padding: 0;
  background-color: #80b977;
  border-radius: 50%;
  font-size: 12vw;
}

#square .title {
  font-size: 5vw;
}

#square .title span:not(:empty) {
  border: 5px solid;
  padding: .6rem 1rem;
  border-radius: 15px;
}

#square .value {
  padding-top: 1.4rem;
}

#square .value:after {
  content: '$';
}

#square .time {
  font-size: 4vw;
}

#square.down {
  background-color: rgba(6, 6, 6, 0.15);
}

.spinner {
  opacity: .3;
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 4px solid;
  border-color: white transparent transparent transparent;
}

@media (min-width: 890px) {
  #square {
    width: 80vw;
    height: 80vw;
  }

  #square .title span:not(:empty) {
    padding: .2em;
  }
}

@media (min-width: 890px) and (orientation: landscape) {
  #square {
    width: 95vh;
    height: 95vh;
  }
}

@media (max-width: 889px) and (orientation: portrait) {
  #square {
    width: 80vh;
    height: 80vh;
  }
}

.currency-changer {
  position: absolute;
  height: 0;
  top: 0.5rem;
  right: 0.5rem;
}




